body {
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  padding: 0;
}
* {
  color: black;
  box-sizing: border-box;
}

/* Main */
/* PROFILE */
.profile-container {
  background-color: #8ec5fc;
  background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  background-color: black;
  height: 100vh;
  padding-top: 15vh;
}
h1,
h2 {
  text-align: center;
}
.profile-container h2 {
  font-size: 40px;
}
.profile-container {
  justify-content: center;
  align-items: center;
}
.profile-container .profileicon {
  position: relative;
  top: 5px;
  font-size: 40px;
}

.profile-context {
  text-align: center;
}

.twittericon {
  margin: 0 auto;
  background-position: center;
  height: 110px;
  border: 1px solid black;
  border-radius: 50%;
}
.profile-context p {
  font-size: 2.5vh;
}
